import React from 'react'
import QrReader from 'react-qr-reader'
function TichDiem() {
  const [ data, setData ] = React.useState('Not found');
  React.useEffect(()=>{

  },[data])
  const handleError = (e)=>{
    console.log(e)
  }
  const handleScan = (e)=>{
    if(e!==null){
      setData(e)
      console.log(e)
    }
  }
    return (
<React.Fragment>
 <div className="content">
  <div className="animated fadeIn">
    <div className="row">
      <div className="col-sm-12">
        <div className="card">
          <div className="card-header">
            <strong className="card-title">Thông tin</strong>
          </div>
          <div className="card-body">
            {/* Credit Card */}
            <QrReader
          delay={300}
          onError={handleError}
          onScan={handleScan}
          style={{ width: '50%',height:'50%' }}
        />
        <p>{data}</p>
            </div>
        </div> {/* .card */}
      </div>{/*/.col*/}
    </div>
  </div>{/* .animated */}
</div>{/* .content */}

        </React.Fragment>
    )
}

export default TichDiem
