import React from 'react'
import host from '../../host/host'

function ThietLapCauHinh() {
    const [apiKey,SetapiKey] = React.useState('')
    const [authDomain,SetauthDomain] = React.useState('')
    const [databaseURL,SetdatabaseURL] = React.useState('')
    const [projectId,SetprojectId] = React.useState('')
    const [storageBucket,SetstorageBucket] = React.useState('')
    const [messagingSenderId,SetmessagingSenderId] = React.useState('')
    const [appId,SetappId] = React.useState('')
    const [measurementId,SetmeasurementId] = React.useState('')

    const [data,SetData] = React.useState([])
    const onThietLapCauHinh = async ()=>{
        
        const trang_thai = data.length > 0 
        const reponse = await fetch(host.ThietLapCauHinhFireBase,{
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({apiKey,authDomain,
                databaseURL,projectId,storageBucket,messagingSenderId,appId,
                measurementId,trang_thai}),
        })
    }
    React.useEffect(async ()=>{
        try {
            const response = await fetch(host.host_firebase)
            const JsonData = await response.json()
            SetData(JsonData)
        } catch (error) {
            
        }
    },[])
    return (
<React.Fragment>
<div>
  {/* Button trigger modal */}
  <button type="button" className="btn btn-primary mt-3" data-toggle="modal" data-target="#exampleModal">
    Thiết lập cấu hình
  </button>
  {/* Modal */}
  <div className="modal fade" id="exampleModal" tabIndex={-1} role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div className="modal-dialog" role="document">
      <div className="modal-content">
        <div className="modal-header">
          <h5 className="modal-title" id="exampleModalLabel">Modal title</h5>
          <button type="button" className="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">×</span>
          </button>
        </div>
        <div className="modal-body">
        <div className="row container">
                    apiKey
                    <input type="text" className="form-control"
                    value={apiKey} onChange={e=>SetapiKey(e.target.value)}
                    ></input>
                </div>
                <div className="row container mt-2">
                    authDomain
                    <input type="text" className="form-control"
                    value={authDomain} onChange={e=>SetauthDomain(e.target.value)}
                    ></input>
                </div>

                <div className="row container mt-2">
                    databaseURL
                    <input type="text" className="form-control"
                    value={databaseURL} onChange={e=>SetdatabaseURL(e.target.value)}></input>
                </div>
                <div className="row container mt-2">
                    projectId
                    <input type="text" className="form-control"
                    value={projectId} onChange={e=>SetprojectId(e.target.value)}></input>
                </div>
                <div className="row container mt-2">
                    storageBucket
                    <input type="text" className="form-control"
                    value={storageBucket} onChange={e=>SetstorageBucket(e.target.value)}></input>
                </div>
                <div className="row container mt-2">
                    messagingSenderId
                    <input type="text" className="form-control"
                    value={messagingSenderId} onChange={e=>SetmessagingSenderId(e.target.value)}></input>
                </div>
                <div className="row container mt-2">
                    appId
                    <input type="text" className="form-control"
                    value={appId} onChange={e=>SetappId(e.target.value)}></input>
                </div>
                <div className="row container mt-2">
                    measurementId
                    <input type="text" className="form-control"
                    value={measurementId} onChange={e=>SetmeasurementId(e.target.value)}></input>
                </div>
        </div>
        <div className="modal-footer">
          <button type="button" className="btn btn-secondary" data-dismiss="modal">Đóng</button>
          <button type="button" className="btn btn-primary" data-dismiss="modal" onClick={onThietLapCauHinh}>Lưu</button>
        </div>
      </div>
    </div>
  </div>
</div>








        </React.Fragment>
    )
}

export default ThietLapCauHinh
