import React from 'react'
import QRCode from "react-qr-code";
import host from '../../../../host/host'
function ThongTin() {

    const [MatKhauCu,SetMatKhauCu] = React.useState('')
    const [MatKhauMoi,SetMatKhauMoi] = React.useState('')
    const [XacNhanMatKhauMoi,SetXacNhanMatKhauMoi] = React.useState('')
    const [DLNguoiDung,SetDLNguoiDung] = React.useState(
        window.localStorage.getItem('dulieunguoidung').length > 0 ?
        JSON.parse(window.localStorage.getItem('dulieunguoidung'))
        : []
    )
    // console.log(DLNguoiDung)

    const onClickCapNhap = async ()=>{
        try {
            const response = await 
            fetch(host.TaiKhoanNguoiDung + `/${DLNguoiDung[0].ten_dang_nhap}/${MatKhauCu}`)
            const dl = await (response.json())
            console.log(!(dl.length > 0) )
            
            if(MatKhauMoi!==XacNhanMatKhauMoi){
                alert('Xác nhận mật khẩu chưa trùng nhau!')
            }else if(MatKhauMoi ==='' &&  XacNhanMatKhauMoi ===''){
                alert('Người dùng chưa nhập mật khẩu mới!')
            }
            else if( !(dl.length > 0)  ){
                alert('Người nhập sai mật khẩu cũ!')
            }
            else{
                const response = await fetch(host.DoiMatKhauTaiKhoanNguoiDung,{
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify({DLNguoiDung,MatKhauMoi}),
                })
                alert(await response.json())
            }
        } catch (error) {
            console.log(error)
            alert('Bạn chưa nhập dữ liệu!')
        }
    }
    return (
        <React.Fragment>
 <div className="content">
  <div className="animated fadeIn">
    <div className="row">
      <div className="col-sm-12">
        <div className="card">
          <div className="card-header">
            <strong className="card-title">Thông tin</strong>
          </div>
          <div className="card-body">
            {/* Credit Card */}
            <div className="row mt-3">
                <div className="col">
                    Tên người dùng
                    <input className="form-control text-center" value={
                    DLNguoiDung.map(x=>x.ten_nhan_vien)
                    } disabled></input>
                </div>
                <div className="col">
                    Mã QR người dùng
                    <br></br>
                    <QRCode value={DLNguoiDung[0].ma_nhan_vien  } />
                </div>
            </div>
            <div className="row mt-3">
                <div className="col">
                    Quyền hạn
                    <input className="form-control" type="text"
                    value={DLNguoiDung[0].quyen} disabled
                    ></input>
                </div>
                <div className="col">
                    Mật khẩu cũ
                    <input className="form-control" type="password" 
                    onChange={e=>SetMatKhauCu(e.target.value)}
                    ></input>
                </div>
            </div>
            <div className="row mt-3">
                <div className="col">
                </div>
                <div className="col">
                    Mật khẩu mới
                    <input className="form-control" type="password"
                    onChange={e=>SetMatKhauMoi(e.target.value)}
                    ></input>
                </div>
            </div>
            <div className="row mt-3">
                <div className="col">
                </div>
                <div className="col">
                    Xác nhận lại mật khẩu mới
                    <input className="form-control" type="password"
                    onChange={e=>SetXacNhanMatKhauMoi(e.target.value)}
                    ></input>
                </div>
            </div>
            <div className="row">
                <div className="col">
                    Ghi chú
                    <input className="form-control" type="text"  
                    value ={DLNguoiDung[0].ghi_chu}
                    disabled></input>
                </div>
            </div>
            <div className="row mt-3">
                <div className="col"></div>
                <div className="col">
                    <div className="row ml-1">
                        <button className="btn btn-primary" 
                        onClick={onClickCapNhap}
                        >Cập nhập dữ liệu</button>
                    </div>
                </div>
            </div>
          </div>
        </div> {/* .card */}
      </div>{/*/.col*/}
    </div>
  </div>{/* .animated */}
</div>{/* .content */}

        </React.Fragment>
    )     

}

export default ThongTin
